<template>
  <div id="intro" class="intro">
      <div class="mainContent"><h1>
              <p>Lat = {{lat}} Lon ={{lon}}</p>
              <p>Gereja Terdekat: {{gereja}}</p>
                <p>{{error}}</p>
            <button @click="myFunction()">Click Me</button></h1>
      </div>
      <div class="lowerEmpty"></div>
  </div>
</template>

<script>
/* eslint-disable */ 
import DB_GEREJA from '../assets/database.json';
import JQuery from 'jquery';
let $ = JQuery ;

export default {
  name: 'IntroSection',
  data: function () {
    return {
        error: '',
        lat:'',
        lon:'',
        gereja:'',
        DBGereja: DB_GEREJA
    }
  },  
    methods:{
        myFunction: function () {	
            if(navigator.geolocation){
                navigator.geolocation.getCurrentPosition(this.calculateShortestChurch);
            }else{
                this.error = "Geolocation is not supported."; 
            }
        },
        calculateShortestChurch:function (position) {	
                this.lat = position.coords.latitude;
                this.lon = position.coords.longitude;

                var shortestDistance = 9999999;
                var objGereja = DB_GEREJA.gereja;

                for (var i = 0; i < objGereja.length; i++) {
                    var curr = this.calcCrow(this.lat, this.lon, objGereja[i].lat, objGereja[i].long);
                    console.log('jarak ', curr);
                    if (curr < shortestDistance) {
                        this.gereja = objGereja[i].nama;
                        shortestDistance = curr;
                    }
                }
        },
            //This function takes in latitude and longitude of two location and returns the distance between them as the crow flies (in km)
        calcCrow(lat1, lon1, lat2, lon2) 
        {
        var R = 6371; // km
        var dLat = this.toRad(lat2-lat1);
        var dLon = this.toRad(lon2-lon1);
        var lat1 = this.toRad(lat1);
        var lat2 = this.toRad(lat2);

        var a = Math.sin(dLat/2) * Math.sin(dLat/2) +
            Math.sin(dLon/2) * Math.sin(dLon/2) * Math.cos(lat1) * Math.cos(lat2); 
        var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
        var d = R * c;
        return d;
        },
        // Converts numeric degrees to radians
        toRad(Value) 
        {
            return Value * Math.PI / 180;
        }
	}
}
</script>

